@import 'utilities';
@import 'animations';

.search {
  background-color: white;
  padding: 0.5em;
  border-radius: 5px;
  margin: 1em;
}

.whole-screen {
  height: 100vh;
  width: 100vw;
  background-color: transparent;
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translateX(50%) translateY(-50%);
  z-index: 1;
}

.btn--submit {
  margin-left: auto;
}

.btn--long {
  width: 100%;
  background-color: rgb(167, 35, 201);
  padding: 0.5em;
  color: white;

  &:first-of-type {
    border-bottom: 1px solid black;
  }
}

.btn--add {
  background-color: inherit;
  margin-bottom: 2em;
}

.btn-nowidth { 
  position: absolute;
  font-size: 1.5em;
  color: rgba(0, 0, 0, 0.4);
  background-color: inherit;
  right: 0.5em;
}

.btn-nowidth:hover {
  cursor: pointer;
}

.relative {
  position: relative;
  border-radius: 5px;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.testing {
  border-radius: 50%;
  font-size: 1em;
  height: 32px;
  width: 32px;
  position: absolute;
  top: -10px;
  right: -10px;
}

.dashboard {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  max-width: 1200px;
}

.total-price {
  padding: 0.5em 2em;
  margin-bottom: 1em;
  background: rgb(7,192,112);
  font-weight: 500;
  color: white;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.15);
}

.small-button {
  padding: 0.5em;
  margin-bottom: 0.5em;
  background-color: rgb(167, 35, 201);
  color: white;
}

.small-button:hover {
  background-color: rgb(119, 24, 143);
}

.wrapper-select--list {
  display: flex;
  align-items: center;
}

.border-green {
  border: 2px solid rgba(7,192,112,0.5);
  border-radius: 5px;
}

.select--list {
  max-width: 300px;
  width: 100%;
  padding: 0.5em 1em;
  font-size: 1.5em;
  font-weight: 500;
  margin: 1em auto;
  /* for Firefox */
  -moz-appearance: none;
  /* for Chrome */
  -webkit-appearance: none;
  text-align: center;
}

.select--sortBy {
  width: fit-content;
  padding: 0.5em;
  // margin-left: 2em;
  /* for Firefox */
  -moz-appearance: none;
  /* for Chrome */
  -webkit-appearance: none;
  text-align: center;
}

.title {
  font-size: 2em;
  font-weight: 500;
  margin: 1em 0;
}

.itemlist {
  display: flex;
  flex-direction: column;
  list-style: none;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  z-index: 3;
}

.modal {
  // display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  overflow: auto;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);
}

.listItem--editing > form {
  z-index: 3;
}

.listItem {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0.5em 0.25em;
  font-size: 1.25em;
  background-color: white;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  transition: all 0.5s ease-out;

  &--unchecked {
    background-color: rgb(233, 233, 233);
  }
  &:hover {
    cursor: pointer;
    background-color: rgba(119, 221, 119, 0.5);
  }
  &--editing {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 3;
    width: 100%;
    height: 100%;
    max-width: 500px;
    max-height: 300px;
    padding: 1em;
    font-size: 1.25em;
    background-color: white;
    border-radius: 5px;
    box-shadow: 8px 8px 16px rgba(0, 0, 0, 0.15);
    // border-bottom: 1px solid rgba(0, 0, 0, 0.15);
    transition: all 0.5s ease-out;
  }
  &--editable {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.5em;
  }
}

.textarea--note {
  margin-top: 10px;
  padding: 0.5em;
  height: 150px;
  width: 75%;
  border-radius: 10px;
  resize: none;
  background-color: rgba(0,0,0,0.1);
}

.icon {
  height: 24px;
  width: 24px;
  margin: 0 5px;
  z-index: 5;
}

.icon:hover {
  cursor: pointer;
}

.wrapper--nav-bar {
  height: 50px;
  max-width: 800px;
  width: 100%;
}

.nav-bar {
  height: 100%;
  display: flex;
  align-items: center;
  /* justify-content: space-around; */
}

.name {
  font-size: 1em;
  background-color: rgb(119, 221, 119);
  padding: 0.5em;
  text-align: center;
}

.name-edit {
  background-color: rgb(255, 116, 116);
  padding: 0.25em;
}

.small-text {
  width: 80%;
  font-size: 1em;
  margin: 0.25em 0;
  text-align: left;
}

.wrap--editing-item {
  position: relative;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-around;
}

.medium-text {
  font-size: 1.5em;
  text-align: center;
  margin: 0.5em 0;
}

.input {
  &--name-edit {
    font-size: 1.25em;
    text-align: center;
    border-radius: 10px;
    margin-bottom: 10px;
    font-weight: 500;
    background-color: rgba(0,0,0,0.1);
    width: 90%;
    padding: 0.25em;
  }
  &--quantity {
    font-size: 0.75em;
    margin-bottom: 5px;
    background-color: rgb(240, 240, 240);
    width: 100%;
  }
}

.label {
  font-size: 0.75em;
  width: 0;
  color: rgba(0, 0, 0, 0.5);

  &-with-width {
    font-size: 0.75em;
    width: initial;
    color: rgba(0, 0, 0, 0.5);
  }
}

.p--strike {
  color: rgba(0,0,0,0.5);
}

.notes {
  font-size: 0.75em;
  max-width: 75%;
  padding: 0 10px;
  color: rgba(0, 0, 0, 0.3);
}

.heading {
  font-size: calc(0.6vw + 1.1em);
  text-align: center;
  font-weight: 500;
  justify-self: flex-start;
}

.input--main {
  background-color: blue;
  margin: 1em 0;
  font-weight: 400;
  padding: 0.5em;
  color: white;
  text-align: center;
}